body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  position:relative;
  border:1px solid #ccc;
  margin:10px;
  padding:10px;
  border-radius:5px;
 }

 .new {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #6bb390;
  color:#fff;
  padding:0px 5px;
  border-radius: 5px;
 }

 .subtitle {
  text-align:left;
  font-size:12px;
  color:#666;
 }

 .title {
  font-size:15px;
  text-align:left;
  width:100%;
  height:30px;
  margin-top:-5px;
 }

 .star {
  color: #fdd835;
  text-align:left;
  font-size: 12px;
 }

 .price {
  text-align:left;
 }

